import React from "react";
import { Paper, Divider, } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import { makeStyles , MuiThemeProvider } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { storiesOf } from "@storybook/react";
import themes from "../../Theme/themes";
import { BrandType } from "../../types/brand";
import Tooltip from "../Tooltip";

const useStyles = makeStyles((theme) => ({
    root: {
        background: theme.palette.background.default,
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
}));

type TooltipStoryProps = {
    mode: string,
    brand: BrandType,
    label: string
}

const TooltipStory = ({
    mode, brand, label
}: TooltipStoryProps) => {
    const classes = useStyles(themes[brand]);

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="Tooltip">
                <Typography variant="h3">Tooltip</Typography>
                <br />
                <Divider />
                <br />
                <Grid >
                    <div style={{marginLeft: 15}}>
                        <Tooltip className={{}} label={label}>
                            <Typography variant="h1">Passe o mouse</Typography>
                        </Tooltip>
                    </div>
                </Grid>
            </Paper>
        </MuiThemeProvider>

    );
};

const argsControls = {
    args: {
        label: "Texto Tooltip",
        brand: "CVC",
        mode: "root",
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
        },
        mode: {
            type: "select",
            options: ["root", "darkBg"]
        },
        label: { type: "string" }
    }
}

storiesOf("Components/Tooltip", module)
    .add("Default", (args) => <TooltipStory {...args}/>, argsControls);
